<template>
  <div
    class="v-top"
    style="position:absolute;z-index:9999"
    :style="{bottom:defaultBottom+'px',right:defaultRight+'px'}"
    v-show="showIcon"
  >
    <i
      :class="types[defaultType]"
      :style="{fontSize:defaultSize+'px',color:defaultColor}"
      @click="goTop()"
    ></i>
  </div>
</template>
<script>
export default {
  props: [
    "type",
    "top",
    "right",
    "bottom",
    "size",
    "color",
    "duration",
    "container"
  ],
  data() {
    return {
      showIcon: false,
      scrollTop: 0,
      types: [
        "iconfont icon-v-totop-0",
        "iconfont icon-v-totop-1",
        "iconfont icon-v-totop-2",
        "iconfont icon-v-totop-3",
        "iconfont icon-v-totop-4",
        "iconfont icon-v-totop-5",
        "iconfont icon-v-totop-6",
        "iconfont icon-v-totop-7",
        "iconfont icon-v-totop-8",
        "iconfont icon-v-totop-9",
        "iconfont icon-v-totop-10",
        "iconfont icon-v-totop-11",
        "iconfont icon-v-totop-12",
        "iconfont icon-v-totop-13",
        "iconfont icon-v-totop-14",
        "iconfont icon-v-totop-15"
      ],
      defaultType: 0,
      defaultTop: 400,
      defaultRight: 30,
      defaultBottom: 30,
      defaultSize: 24,
      defaultColor: "#EEE",
      defaultDuration: 300,
      defaultContainer: document.documentElement || document.body
    };
  },
  beforeDestroy() {
    this.defaultContainer.removeEventListener(
      "scroll",
      this.handelScroll,
      false
    );
  },
  mounted() {
    this.type ? (this.defaultType = this.type) : "";
    this.top ? (this.defaultTop = this.top) : "";
    this.right ? (this.defaultRight = this.right) : "";
    this.bottom ? (this.defaultBottom = this.bottom) : "";
    this.size ? (this.defaultSize = this.size) : "";
    this.color ? (this.defaultColor = this.color) : "";
    this.duration ? (this.defaultDuration = this.duration) : "";
    this.defaultContainer =
      document.querySelector(this.container) || this.defaultContainer;
    this.defaultContainer.addEventListener("scroll", this.handelScroll, false);
  },
  methods: {
    handelScroll() {
      this.scrollTop =
        this.defaultContainer.scrollTop ||
        window.pageYOffset ||
        this.defaultContainer.scrollTop;
      this.scrollTop > this.defaultTop
        ? (this.showIcon = true)
        : (this.showIcon = false);
      //   console.log(this.scrollTop, this.defaultTop);
    },
    goTop() {
      window.requestAnimationFrame = (function() {
        return (
          window.requestAnimationFrame ||
          window.webkitRequestAnimationFrame ||
          window.mozRequestAnimationFrame ||
          function(callback) {
            window.setTimeout(callback, 1000 / 60);
          }
        );
      })();
      var step = (this.scrollTop / (this.defaultDuration / (1000 / 60))) >> 0;
      var self = this;
      function fn() {
        if (self.scrollTop >= 0) {
          self.scrollTop -= step;
          self.defaultContainer.scrollTop = self.scrollTop;
          fn.rafTimer = requestAnimationFrame(fn);
        } else {
          self.defaultContainer.scrollTop = 0;
          cancelAnimationFrame(fn.rafTimer);
        }
      }
      fn.rafTimer = requestAnimationFrame(fn);
    }
  }
};
</script>
<style>
@font-face {
  font-family: "iconfont";
  src: url("//at.alicdn.com/t/font_928263_1i5c1m9o19x.eot?t=1542792111670"); /* IE9*/
  src: url("//at.alicdn.com/t/font_928263_1i5c1m9o19x.eot?t=1542792111670#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("data:application/x-font-woff;charset=utf-8;base64,")
      format("woff"),
    url("//at.alicdn.com/t/font_928263_1i5c1m9o19x.ttf?t=1542792111670")
      format("truetype"),
    /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
      url("//at.alicdn.com/t/font_928263_1i5c1m9o19x.svg?t=1542792111670#iconfont")
      format("svg"); /* iOS 4.1- */
}

.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  cursor: pointer;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.v-top {
  transition: all 0.5s;
  animation: opacity 0.5s e;
}
@keyframes opacity {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
.icon-v-totop-15:before {
  content: "\e600";
}

.icon-v-totop-14:before {
  content: "\e67a";
}

.icon-v-totop-13:before {
  content: "\e609";
}

.icon-v-totop-12:before {
  content: "\e85d";
}

.icon-v-totop-11:before {
  content: "\e6a2";
}

.icon-v-totop-10:before {
  content: "\e63a";
}

.icon-v-totop-9:before {
  content: "\e649";
}

.icon-v-totop-8:before {
  content: "\e64a";
}

.icon-v-totop-7:before {
  content: "\e64b";
}

.icon-v-totop-6:before {
  content: "\e660";
}

.icon-v-totop-5:before {
  content: "\e70b";
}

.icon-v-totop-4:before {
  content: "\e633";
}

.icon-v-totop-3:before {
  content: "\e612";
}

.icon-v-totop-2:before {
  content: "\e616";
}

.icon-v-totop-1:before {
  content: "\e619";
}

.icon-v-totop-0:before {
  content: "\e658";
}
</style>



